<template>
  <div class='container'>
    <nut-navbar
      style="background-color:rgba(0,0,0,0.2); color: #fff; height:55px; line-height: 55px;"
        @on-click-back="NavBarLeft"
        :leftShow="true"
        :rightShow="false"
    >{{title}}</nut-navbar>
    <van-cell center title="编辑模式">
      <van-switch v-model="fix"  slot="right-icon" size="24" />
    </van-cell>
    <div v-if="this.title !=='我的图片'">
      <van-list v-if="!this.fix"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell @click="jumpTo(`https://${item.url}`)" v-for="item in list" :key="item.index">
          <div class="cell">
            <div v-if="!item.cover.images.length" class="noImg">{{item.title}}</div>
            <van-image
            v-else
            width="60px"
            height="60px"
            :src='item.cover.images[0]'
            />
            <div class="cell_right">
              <div class="cell_text">{{item.title}}</div>
              <div class="cell_url"> {{`https://${item.url}`}}</div>
            </div>
          </div>
        </van-cell>
      </van-list>
      <van-list v-else
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell @click="toEdit(item)" v-for="item in list" :key="item.index">
          <div class="cell">
            <div v-if="!item.cover.images.length" class="noImg">{{item.title}}</div>
            <van-image
            v-else
            width="60px"
            height="60px"
            :src='item.cover.images[0]'
            />
            <div class="cell_right">
              <div class="del" @click.stop="delArticles(item.id)">+</div>
              <div class="cell_text">{{item.title}}</div>
              <div class="cell_url"> {{`https://${item.url}`}}</div>
            </div>
          </div>
        </van-cell>
        <van-cell v-if="this.fix">
          <div class="plus" @click="toPublish">新增</div>
        </van-cell>
      </van-list>
    </div>
    <div v-else>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="imgList" v-for="item in list" :key="item.index">
          <van-image
          style="vertical-align: middle;"
          @click="jumpTo(item.url)"
            width="100%"
            height="170px"
            :src="item.url"
          />
            <div v-if="fix" class="delImg" @click.stop="delImage(item.id)">+</div>
        </div>
        <van-cell v-if="this.fix">
          <div class="plus" @click="addImage">新增</div>
        </van-cell>
        <div></div>
      </van-list>
    </div>
      <van-pagination
        v-model="currentPage"
        :page-count='totalPage'
        mode="simple"
      />
      <van-popup  v-model="show" @close="id = null">
        <div class="publish" v-if="this.title !=='我的图片'" >
          <van-field
            v-model="content.title"
            name="简介标题"
            label="简介标题"
            placeholder="请填写简介标题"
            :rules="[{ required: true, message: '请填写简介标题' }]"
          />
          <van-field
            v-model="content.url"
            name="地址"
            label="网络地址"
            placeholder="请填写网址"
            :rules="[{ required: true, message: '请填写网址' }]"
          />
          <div style="margin-top: 16px;display:flex">
            <nut-button
                v-if="!this.id"
                @click="publishArticles"
                style="background-color:rgba(0,0,0,0.1);font-size : 16px"
                block
                color="#000"
                type="light"
              >
              确定
            </nut-button>
            <nut-button
                v-else
                @click="editArticles"
                style="background-color:rgba(0,0,0,0.1);font-size : 16px"
                block
                color="#000"
                type="light"
              >
              修改
            </nut-button>
            <nut-button
                @click="cancel"
                style="background-color:rgba(0,0,0,0.1);font-size : 16px"
                block
                color="#000"
                type="light"
              >
              取消
            </nut-button>
          </div>
        </div>
        <div class="uploader" v-else>
            <h3>图片上传<span style="color:red;">（暂不支持上传同名图片）</span></h3>
            <nut-uploader
              name="image"
              :headers="headers"
              url="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
              :isPreview="true"
              :acceptType = "['image/jpeg', 'image/png', 'image/gif', 'image/bmp']"
              @success="onSuccess"
              @preview="onPreview"
              @progress="onProgress"
              @start ="onStart"
              :xhrState="201"
              typeError="对不起，不支持上传该类型文件！"
              limitError="对不起，文件大小超过限制！"
            >
            <div>
              <div class="imgBorder">
                <van-image
                  v-if="this.imageData"
                  style="vertical-align: middle;"
                    width="100px"
                    height="100px"
                    :src="this.imageData"
                  />
                  <van-icon v-else name="photo" size="100px"/>
              </div>
            </div>
            </nut-uploader>
            <div class="progress">
              <nut-progress
            :percentage="progressNum"
            :showText="false"
            strokeWidth="15"
            stroke-color="#4bb2b2"
            status="active"
            />
            </div>

            <div style="margin-top: 16px;display:flex">
                <nut-button
                    @click="cancel"
                    style="background-color:rgba(0,0,0,0.1);font-size : 16px"
                    block
                    color="#000"
                    type="light"
                  >
                  确定
                </nut-button>
                <nut-button
                    @click="cancelImg"
                    style="background-color:rgba(0,0,0,0.1);font-size : 16px"
                    block
                    color="#000"
                    type="light"
                  >
                  取消
                </nut-button>
              </div>
        </div>
      </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { DelImage, GetImages, EditArticles, DelArticles, GetArticles, PublishArticles } from '@/request/api'
export default {
  data () {
    return {
      progressNum: 0,
      flag: false,
      imageID: null,
      imageData: null,
      headers: {
        Authorization: ``
      },
      list2: null,
      totalPage: 0,
      currentPage: 1,
      channelId: null,
      timestamp: Date.now(),
      id: null,
      show: false,
      fix: false,
      list: [],
      title: '标题',
      loading: false,
      finished: false,
      content: {
        url: '',
        title: ''
      },
      publish: {
        title: '',
        content: '',
        cover: {
          type: 0,
          images: []
        },
        channel_id: 4
      }
    }
  },
  methods: {
    async onStart () {
      this.progressNum = 0
      this.list2 = await GetImages({ page: 1, per_page: 50 })
    },
    onProgress (data, numerator, denominator) {
      this.progressNum = Math.ceil(numerator * 100 / denominator)
    },
    async onSuccess (res, req) {
      this.imageID = JSON.parse(req).data.id
      this.$nextTick(() => {
        this.list2.results.forEach((item, index, arr) => {
          if (item.id === this.imageID) {
            this.$toast.fail('图片重名！请重新选择图片')
            this.imageData = null
            this.progressNum = 0
          }
        })
      })
    },
    onPreview (res) {
      this.imageData = res
    },
    addImage (item) {
      this.progressNum = 0
      this.show = true
    },
    cancel () {
      if (this.imageData) {
        setTimeout(() => {
          this.show = false
          this.timestamp = Date.now()
        }, 1000)
      } else {
        this.show = false
        this.timestamp = Date.now()
      }
    },
    toEdit (item) {
      this.show = true
      this.content.title = item.title
      this.content.url = item.url
      this.id = item.id
    },
    del (e) {
      this.list.forEach(function (item, index, arr) {
        if (item.id === e) {
          arr.splice(index, 1)
        }
      })
    },
    jumpTo (e) {
      if (this.fix) {
        return false
      } else {
        window.open(e)
      }
    },
    NavBarLeft () {
      this.$router.push('Home')
    },
    NavBarRight () {
      this.showPopup()
    },
    onLoad () {
      this.loading = false
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1)
      //   }

      //   // 加载状态结束
      //   this.loading = false

      //   // 数据全部加载完成
      //   if (this.list.length >= 10) {
      //     this.finished = true
      //   }
      // }, 1000)
    },
    async delArticles (id) {
      DelArticles(id)
      this.$toast.success('删除成功')
      this.del(id)
    },
    async getArticles () {
      const data = await GetArticles({ page: this.currentPage })
      data.results.forEach(function (item, index, arr) {
        item.url = item.title.split('<hr>')[1]
        item.title = item.title.split('<hr>')[0]
      })
      this.list = data.results
      this.totalPage = Math.ceil(data.total_count / 10)
    },
    editArticles () {
      if (this.content.title && this.content.url) {
        this.publish.id = this.id
        this.publish.title = `${this.content.title}<hr>${this.content.url}`
        this.publish.content = '这里是草稿'
        EditArticles(this.publish, true)
        setTimeout(() => {
          this.cancel()
          this.$toast.success('修改成功')
        }, 1000)
      } else {
        this.$toast.fail('简介标题或网络地址不能为空')
      }
    },
    publishArticles () {
      if (this.content.title && this.content.url) {
        this.publish.title = `${this.content.title}<hr>${this.content.url}`
        this.publish.content = '这里是草稿'
        PublishArticles(this.publish, true)

        setTimeout(() => {
          this.cancel()
          this.$toast.success('操作成功')
        }, 1000)
      } else {
        this.$toast.fail('简介标题或网络地址不能为空')
      }
    },
    toPublish () {
      this.show = true
    },
    async getImages () {
      const data = await GetImages({ page: this.currentPage, per_page: 3 })
      this.list = data.results
      this.totalPage = Math.ceil(data.total_count / 3)
    },
    async cancelImg () {
      if (this.imageData && this.imageID) {
        DelImage(this.imageID)
        this.imageID = null
        this.imageData = null
        this.progressNum = 0
      }
      this.show = false
      this.timestamp = Date.now()
    },
    delImage (id) {
      DelImage(id)
      this.$toast.fail('删除成功')
      this.del(id)
    }
  },
  created () {
    this.headers = {
      Authorization: `Bearer ${this.customer.token}`
    }
    this.title = this.$route.params.title
    this.$nextTick(() => {
      if (this.title === '我的图片') {
        this.getImages()
      } else {
        this.getArticles()
      }
    })
  },
  watch: {
    progressNum () {

    },
    currentPage () {
      if (this.title === '我的图片') {
        this.getImages()
      } else {
        this.getArticles()
      }
    },
    timestamp () {
      if (this.title === '我的图片') {
        this.getImages()
        this.imageData = null
      } else {
        this.getArticles()
      }
    },
    title () {
      if (!this.title) {
        this.$router.push('Home')
      }
    }
  },
  computed: {
    ...mapState(['customer'])
  }
}
</script>

<style lang='less'>
.imgList{
  width: 100%;
  position: relative;
  .delImg{
    position: absolute;
    color: red;
    font-size: 30px;
    transform:rotate(45deg);
    position: absolute;
    top: 5px;
    right: 5px;
  }
}
.uploader{
  width: 375px;
  text-align: center;
  .imgBorder{
    padding: 30px;
    border: 1px dashed #ccc;
  }
  .progress{
    margin-top: 10px;
    display: inline-block;
    width: 60%;
}
}
.imgPlus{
  background-color: #ccc;
  color: #fff;
  display: inline-block;
  text-align: center;
  line-height: 170px;
  vertical-align: middle;
  height:170px;
  width: 50%;
}
.van-pagination {
 background-color: #fff;
}
.publish{
  width:375px;
}
.plus{
  text-align: center;
  border: 1px solid #ccc;
}
.cell{
  display: flex;
  position: relative;
  height: 70px;
  padding: 5px;
  box-sizing: border-box;
  .noImg{
    overflow: hidden;
    padding: 5px;
    box-sizing: border-box;
    width: 60px;
    height: 60px;
    line-height: 48px;
    text-align: center;
    border: 1px solid #ccc;
    font-size: 18px;
  }
  .cell_right{
    .del{
      color: red;
      font-size: 30px;
      transform:rotate(45deg);
      position: absolute;
      top: 5px;
      right: 5px;
    }
    padding: 5px;
    padding-left: 30px;
    box-sizing: border-box;
    .cell_text{
      height: 32px;
      box-sizing: border-box;
      padding-bottom: 5px;
    }
    .cell_url{
      font-size: 12px;
      color:blue;
    }
  }
}
</style>
