import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Icon, Pagination, Popup, Switch, List, Image, Field, Cell, CellGroup, Form, Toast, Overlay } from 'vant'

import { Progress, Button, Col, Row, NavBar, Uploader } from '@nutui/nutui'

Vue.use(Icon)

Vue.use(Pagination)

Vue.use(Popup)

Vue.use(Switch)

Vue.use(List)

Vue.use(Overlay)

Progress.install(Vue)
Uploader.install(Vue)
Button.install(Vue)
Col.install(Vue)
Row.install(Vue)
NavBar.install(Vue)

Vue.config.productionTip = false
Vue.use(Image)
Vue.use(Field)
Vue.use(Cell)
Vue.use(CellGroup)
Vue.use(Form)
Vue.use(Toast)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
