import itcast from '@/request/itcast'
import apiopen from '@/request/apiopen'
export const Login = data => itcast('mp/v1_0/authorizations', 'post', {
  'mobile': '17000009410',
  'code': '246810'
})
// 文章相关
// 获取文章
export const GetArticles = (data) => itcast('/mp/v1_0/articles', 'get', data)
// 新增文章
export const PublishArticles = (data, draft) => {
  if (draft) {
    itcast('mp/v1_0/articles?draft=true', 'post', data)
  } else {
    itcast('mp/v1_0/articles', 'post', data)
  }
}
// 修改文章
export const EditArticles = (data, draft) => {
  if (draft) {
    itcast(`/mp/v1_0/articles/${data.id}?draft=true`, 'PUT', data)
  } else {
    itcast(`mp/v1_0/articles/${data.id}`, 'PUT', data)
  }
}
// 删除文章
export const DelArticles = (data) => itcast(`/mp/v1_0/articles/${data}`, 'DELETE')
// 获取图片信息
export const GetImages = (data) => itcast(`/mp/v1_0/user/images`, 'get', data)
// 删除图片
export const DelImage = data => itcast(`/mp/v1_0/user/images/${data}`, 'DELETE')
export const LoginUser = data => apiopen('loginUser', 'post', data)
export const DeveloperLogin = data => apiopen('developerLogin', 'post', data)
