import axios from 'axios'
import store from '@/store'
import router from '@/router'
import JSONBIG from 'json-bigint'

axios.defaults.transformResponse = [(data) => {
  // 后台的原始数据   理想情况 json字符串
  // 后台可能没有任何响应内容  data 值是 null
  try {
    return JSONBIG.parse(data)
  } catch (e) {
    return data
  }
}]
const instance = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/',
  // 设置默认请求地址
  headers: {
    'Content-Type': 'application/json;charset=UTF-8',
    'Access-Control-Allow-Origin': '*'
    // 设置默认请求头
  }
})
instance.interceptors.request.use((config) => {
  // 设置token请求头
  config.headers.Authorization = `Bearer ${store.state.customer.token}`
  // 加loading
  return config
})
instance.interceptors.response.use((res) => {
  // 简化数据
  // 更新token
  try {
    return res.data.data
  } catch (e) {}
  return res
}, async err => {
  if (err.response && err.response.status === 401) {
    const loginConfig = { path: '/login', query: { redirectUrl: router.currentRoute.path } }
    // 收集信息
    // const user = store.state.customer
    return router.push(loginConfig)
  }
  return Promise.reject(err)
})
export default (url, method, data) => {
  return instance({
    method,
    url,
    [method.toLowerCase() === 'get' ? 'params' : 'data']: data
    // 判断请求方式 来决定是data传参还是params传参
  })
}
